*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'GothamPro';
  font-weight: 400;
  font-size: 14px;
  color: #000000;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

@media (max-width: 767px) {
  body {
    font-size: 10px;
  }
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
  background: none;
  padding: 0;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'GothamPro';
