.main {
  display: flex;
  position: relative;
}

.main__column:first-child {
  width: 696px;
  position: relative;
}

.main__column:last-child {
  width: calc(100% - 696px);
  padding-left: 40px;
}

.btnblock {
  margin-bottom: 60px;
}

.constructdoor {
  fill: #C4C4C4;
}

.constructdoor:hover {
  fill: black;
}

@media (min-width: 1280px) {
  .stickyblock {
    position: sticky;
    width: 696px;
    left: 0;
    top: 0;
  }
}

@media (max-width: 767px) {
  .stickyblock {
    max-width: 370px;
    margin: 0 auto;
    width: 100%;
    position: relative;
  }
}

@media (max-width: 1279px) {
  .main {
    flex-direction: column;
    align-items: center;
  }
  .main__column:first-child {
    width: 100%;
    padding: 0 25px;
    position: fixed;
    top: 0;
    left: 0;
    padding-bottom: 17px;
    background: white;
    z-index: 10;
  }
  .main__column:first-child:after {
    content: "";
    position: absolute;
    bottom: -31px;
    left: 0;
    width: 100%;
    height: 31px;
    background: linear-gradient(180deg, #DCDCDC 0%, #FFFFFF 100%);
  }
  .main__column:last-child {
    max-width: 768px;
    width: 100%;
    padding: 0 25px;
  }
}

@media (max-width: 767px) {
  .main {
    padding-top: 350px;
  }
}

.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
}

.blocktop {
  display: flex;
  align-items: center;
  padding: 25px 0px;
}

.blocktop._left li:first-child {
  margin-right: 109px;
}

.blocktop._right {
  justify-content: space-between;
}

.blocktop._right li:last-child .printorder {
  margin-right: 48px;
}

@media (max-width: 767px) {
  .blocktop li {
    position: absolute;
  }
  .blocktop._left li {
    transform: rotate(-90deg);
  }
  .blocktop._left li:first-child {
    margin-right: 0px;
    bottom: 99px;
    left: -40px;
  }
  .blocktop._left li:last-child {
    bottom: 67px;
    right: -5px;
  }
  .blocktop._right {
    padding: 0;
  }
  .blocktop._right .printorder {
    display: none;
  }
  .blocktop._right li:first-child {
    display: none;
  }
  .blocktop._right li:last-child {
    position: fixed;
    top: 23px;
    right: 23px;
    z-index: 20;
  }
}

@media (max-width: 1279px) {
  .blocktop._right {
    padding: 0;
  }
  .blocktop._right .printorder {
    display: none;
  }
  .blocktop._right li:first-child {
    display: none;
  }
  .blocktop._right li:last-child {
    position: fixed;
    top: 23px;
    right: 23px;
  }
}

