    height: 0;
    margin-bottom: 20px;
  }
  .techfeatures__title + .radiobtn {
    width: 120px;
  }
  .techfeatures .radiobtn {
    display: inline-block;
  }
}

.radiobtn {
  display: block;
  margin-bottom: 18px;
}

.radiobtn input {
  display: none;
}

.radiobtn input:checked + span:after {
  display: block;
}

.radiobtn span {
  display: block;
  position: relative;
  padding-left: 30px;
  font-size: 14px;
}

.radiobtn span:after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 4px;
  width: 9px;
  height: 9px;
  background: #5891FF;
  border-radius: 100%;
  display: none;
}

.radiobtn span:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 17px;
  height: 17px;
  border: 2px solid #737373;
  border-radius: 100%;
}

.radiobtn span:hover {
  cursor: pointer;
}

