@import "import/reset";
@import "import/fonts";

.main {
    display: flex;
    position: relative;

    &__column {
        &:first-child {
            width: 696px;
            position: relative;
        }

        &:last-child {
            width: calc(100% - 696px);
            padding-left: 40px;
        }
    }
}

.btnblock {
    margin-bottom: 60px;
}

.constructdoor {
    fill: #C4C4C4;

    &:hover {
        fill: black;
    }
}

.stickyblock {
    @media (min-width: 1280px) {
        position: sticky;
        width: 696px;
        left: 0;
        top: 0;
    }

    @media (max-width: 767px) {
        max-width: 370px;
        margin: 0 auto;
        width: 100%;
        position: relative;
        // position: fixed;
        // top: 0;
        // left: 0;
        // width: 370px;
        // height: 320px;
        // background: white;
        // z-index: 10;
    }
}

@media (max-width: 1279px) {
    .main {
        flex-direction: column;
        align-items: center;

        &__column {
            &:first-child {
                width: 100%;
                padding: 0 25px;
                position: fixed;
                top: 0;
                left: 0;
                padding-bottom: 17px;
                background: white;
                z-index: 10;

                &:after {
                    content: "";
                    position: absolute;
                    bottom: -31px;
                    left: 0;
                    width: 100%;
                    height: 31px;
                    background: linear-gradient(180deg, #DCDCDC 0%, #FFFFFF 100%);
                }
            }

            &:last-child {
                max-width: 768px;
                width: 100%;
                padding: 0 25px;
            }
        }
    }
}

@media (max-width: 767px) {
    .main {
        padding-top: 350px;
    }
}

.container {
    max-width: 1170px;
    width: 100%;
    margin: 0 auto;
}

.blocktop {
    display: flex;
    align-items: center;
    padding: 25px 0px;

    &._left {
        li {
            &:first-child {
                margin-right: 109px;
            }
        }
    }

    &._right {
        justify-content: space-between;

        li {
            &:last-child {
                .printorder {
                    margin-right: 48px;
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .blocktop {
        li {
            position: absolute;
        }

        &._left {
            li {
                transform: rotate(-90deg);

                &:first-child {
                    margin-right: 0px;
                    bottom: 99px;
                    left: -40px;
                }

                &:last-child {
                    bottom: 67px;
                    right: -5px;
                }
            }
        }

        &._right {
            padding: 0;

            .printorder {
                display: none;
            }

            li {
                &:first-child {
                    display: none;
                }

                &:last-child {
                    position: fixed;
                    top: 23px;
                    right: 23px;
                    z-index: 20;
                }
            }
        }
    }
}

@media (max-width: 1279px) {
    .blocktop {
        &._right {
            padding: 0;

            .printorder {
                display: none;
            }

            li {
                &:first-child {
                    display: none;
                }

                &:last-child {
                    position: fixed;
                    top: 23px;
                    right: 23px;
                }
            }
        }
    }
}

.orderinfo {
    &__article {
        margin-bottom: 8px;
    }

    &__bold {
        font-size: 28px;
        font-weight: 500;
        text-transform: uppercase;
    }
}

@media (max-width: 767px) {
    .orderinfo {
        &__article {
            font-size: 10px;
            margin-bottom: 4px;
        }

        &__bold {
            font-size: 14px;
        }
    }
}

.mainslider {
    padding-bottom: 25px;

    &__item {
        display: flex;
        justify-content: center;
    }

    .swiper-pagination {
        bottom: 0;
    }

    .swiper-pagination-bullet {
        width: 11px;
        height: 11px;
        opacity: 1;
        background: #C4C4C4;

        @media (max-width: 767px) {
            width: 5px;
            height: 5px;
        }
    }

    .swiper-pagination-bullet-active {
        background: #5891FF;
    }

    .swiper-pagination-bullets .swiper-pagination-bullet {
        margin: 0 10px;

        @media (max-width: 767px) {
            margin: 0 3px;
        }
    }

    @media (max-width: 767px) {
        width: 250px;
    }
}

.orgname {
    font-weight: 500;
    margin-bottom: 6px;
}

.output {
    font-weight: 500;
    border-bottom: 1px solid black;
}

.stepsorder {
    &__title {
        font-size: 16px;
        font-weight: 700;
        padding: 21px 0px;
        margin-bottom: 33px;
        border-bottom: 1px solid #C4C4C4;
        position: relative;
        cursor: pointer;

        &:after {
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 10px;
            height: 12px;
            background: url("../img/expandmore.svg") no-repeat center;
            transform: rotate(180deg);
        }
    }

    &__title.is-active {
        &:after {
            transform: rotate(0deg);
        }
    }

    &__item {
        margin-bottom: 60px;
    }
}

@media (max-width: 767px) {
    .stepsorder {
        &__title {
            font-size: 12px;

            &:after {
                top: 42%;
            }
        }
    }
}

.contheader {
    font-size: 22px;
    font-weight: 400;

    @media (max-width: 767px) {
        font-size: 18px;
    }
}

.contheader._checktitle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.doortype {
    &__img {
        flex: 0 0 auto;
        padding-right: 23px;
        cursor: pointer;
        opacity: 0.5;

        &:hover {
            opacity: 1;
        }
    }

    &__article {
        margin-bottom: 15px;
    }

    &__title {
        margin-bottom: 23px;
    }
}

.doortypeslider {
    margin-bottom: 14px;

    .swiper-wrapper {
        align-items: self-end;
    }

    .swiper-slide {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        height: 100%;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__img {
        opacity: 0.5;
        cursor: pointer;

        &:hover {
            opacity: 1;
        }
    }
}

.doordimensions {
    display: flex;

    &__title {
        margin-bottom: 8px;
    }

    &__size {
        font-size: 22px;
    }

    li {
        &:first-child {
            margin-right: 41px;
        }
    }
}

@media (max-width: 767px) {
    .doordimensions {
        &__size {
            font-size: 18px;
        }
    }
}

.techfeatures {
    display: flex;

    &__title {
        height: 30px;
        margin-bottom: 15px;
    }

    li {
        &:not(:last-child) {
            margin-right: 30px;
        }
    }
}

@media (max-width: 767px) {
    .techfeatures {
        display: block;

        br {
            display: none;
        }

        &__title {
            height: 0;
            margin-bottom: 20px;
        }

        &__title+.radiobtn {
            width: 120px;
        }

        .radiobtn {
            display: inline-block;
        }
    }
}

.radiobtn {
    display: block;
    margin-bottom: 18px;

    input {
        display: none;
    }

    input:checked+span:after {
        display: block;
    }

    span {
        display: block;
        position: relative;
        padding-left: 30px;
        font-size: 14px;

        &:after {
            content: "";
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 4px;
            width: 9px;
            height: 9px;
            background: #5891FF;
            border-radius: 100%;
            display: none;
        }

        &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 17px;
            height: 17px;
            border: 2px solid #737373;
            border-radius: 100%;
        }

        &:hover {
            cursor: pointer;
        }
    }
}

.doorview {
    display: flex;
    padding-top: 49px;

    &__type {
        width: 70px;
        position: relative;
        top: -20px;

        li {
            &:first-child {
                margin-bottom: 32px;
            }
        }
    }

    &__content {
        position: relative;

        &:after {
            content: "";
            position: absolute;
            top: 11px;
            left: -71px;
            transform: translateY(-50%);
            width: 70px;
            height: 1px;
            background: black;
        }

        &:before {
            content: "";
            position: absolute;
            top: 11px;
            right: -71px;
            transform: translateY(-50%);
            width: 70px;
            height: 1px;
            background: black;
        }
    }

    &__img {
        width: 238px;
    }
}

@media (max-width: 767px) {
    .doorview {
        &__img {
            width: 150px;
        }

        &__type {
            width: 43px;
            margin-right: 20px;

            li {
                &:first-child {
                    margin-bottom: 40px;
                }
            }
        }

        &__content {
            position: relative;

            &:after {
                left: -63px;
                width: 43px;
            }

            &:before {
                right: -63px;
                width: 43px;
            }
        }
    }
}

.doorsizes {
    display: flex;
    align-items: center;
    margin-bottom: 50px;

    &__column {
        &:first-child {
            margin-right: 119px;
        }
    }

    &__list {
        li {
            margin-bottom: 24px;
        }
    }

    &__title {
        color: #737373;
        margin-bottom: 10px;
    }

    &__link {
        width: 150px;
    }
}

@media (max-width: 767px) {
    .doorsizes {
        &__column {
            &:first-child {
                margin-right: 60px;
            }
        }
    }
}

.inpblock {
    &__title {
        margin-bottom: 10px;
    }

    &__field {
        font-size: 33px;
        color: #C4C4C4;
        border: none;
        outline: none;
        width: 101px;
        height: 35px;
        border-bottom: 1px solid #737373;
    }
}

.contentlink {
    text-decoration: underline;
    position: relative;
    display: inline-block;
    padding-right: 30px;
    line-height: 22px;

    &:after {
        content: "";
        position: absolute;
        top: 8px;
        right: 0px;
        transform: translateY(-50%);
        width: 18px;
        height: 18px;
        background: url("../img/link-icon.svg") no-repeat center;
    }
}

.contentlink._mb {
    margin-bottom: 35px;
}

.btn {
    max-width: 403px;
    width: 100%;
    height: 80px;
    background: #5891FF;
    border-radius: 5px;
    font-size: 18px;
    color: white;
}

.btn._linkicon {
    display: inline-block;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 28px;
    padding-right: 28px;
}

.btn._mt {
    margin-top: 70px;
}

.btn._mb {
    margin-bottom: 50px;
}

@media (max-width: 767px) {
    .btn {
        height: 50px;
    }
}

.finishing {
    &__title {
        margin-bottom: 10px;
    }
}

.optionlist {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 40px;

    &__item {
        width: 72px;
        position: relative;
        cursor: pointer;
        margin-bottom: 10px;
    }

    &__item._middlewidth {
        width: 102px;
        padding-bottom: 15px;
    }

    &__item._bigwidth {
        width: 143px;
    }

    &__item.is-active {
        position: relative;
        color: white;

        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 87px;
            background: #5891FF;
        }
    }

    &__item._bigwidth.is-active,
    &__item._midllewidth.is-active {
        &:after {
            bottom: -20px;
            left: 0;
            height: 99px;
        }
    }

    &__img {
        width: 53px;
        margin: 0 auto;
        position: relative;
        z-index: 1;
        text-align: center;
    }

    &__img._middlesize {
        width: 91px;
    }

    &__img._bigsize {
        width: 119px;
    }

    &__article {
        font-size: 13px;
        text-align: center;
        word-wrap: break-word;
        padding: 5px 5px 14px;
        position: relative;
        z-index: 1;
    }
}

@media (max-width: 767px) {
    .optionlist {
        &__item {
            width: 58px;
            margin-right: 5px;
        }

        &__item._middlewidth {
            width: 63px;
        }

        &__item._bigwidth {
            width: 96px;
        }

        &__img._middlesize {
            width: 55px;
        }

        &__img._bigsize {
            width: 80px;
        }

        &__article {
            font-size: 10px;
            padding: 0px 5px 10px;
        }
    }
}

